const state = {
  users: null,
}

const mutations = {
  SET_USERS(state, users) {
    state.users = users
  },
}

const getters = {
  users: state => state.users,
  managers: state =>
    state.users
      ? state.users.filter(u => ['manager', 'owner'].includes(u.role))
      : [],
}

export default {
  state,
  mutations,
  getters,
}
